<template>
    <div class="container">
      <el-row>
        <el-col :span="8">
          <h1 class="title">新增问题单</h1>
          
           
          <label for="">订单ID</label><el-input v-model="task.order_id" placeholder="订单ID"></el-input>
          <label for="">problem</label><el-input v-model="task.problem" placeholder="problem"></el-input>
          <label for="">状态</label><el-input v-model="task.status" placeholder="状态"></el-input>
          <label for="">问题描述</label><el-input v-model="task.description" placeholder="问题描述"></el-input>
          <label for="">跟踪号</label><el-input v-model="task.track_number" placeholder="跟踪号"></el-input>
          <label for="">新价格</label><el-input v-model="task.new_price" placeholder="新价格"></el-input>
           
        </el-col>
      </el-row>
      <div class="footerButton border-shadow">
        <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
      </div>
    </div>
</template>

<script>
    export default {
      name: "addTask",
      data() {
          return { 
            pageName: '添加',
            task: {
              order_id: "",
              problem: "",
              status: "",
              description: "",
              track_number: "" ,
              new_price: ""  
            },
           
            warehouseOptions: [
              {
                label: '国内仓',
                value: '1'
              },
              {
                label: '泰国仓',
                value: '2'
              } 
            ],
            

          }
      },
      methods: {
        submitTask() {
          let url = "http://api.orkowms.me/problems";
          let params = {};
          params.order_id = this.task.order_id;
          params.problem = this.task.problem;
          params.status = this.task.status;
          params.description = this.task.description;
          params.track_number = this.task.track_number;
          params.new_price = this.task.new_price;
           
          let that = this;  
          this.$axios.post(url,params).then((res) => {
             
              that.$message({
                type: 'success',
                message: '新增成功!'
              }); 
            
          }).catch((err) => {
             
              that.$message({
                type: 'error',
                message: '新增失败!'
              }); 
          })
        },
         
      },
      created() {
         
      }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



